import topBox from '@/components/mbbo/topBox/topBox';
import putInOne from './putInOne/putInOne';
import putInTwo from './putInTwo/putInTwo';
import { mapMutations, mapGetters } from 'vuex';
export default {
	name: 'putInBusiness',
	components: {
		// empty,
		topBox,
		putInOne,
		putInTwo
	},
	data() {
		return {
			putInstate:1,
			content:1
		};
	},
	computed: {
		...mapGetters(['GET_ALITTLE'])
	},
	created() {},
	watch: {
		GET_ALITTLE: {
			handler(a, b) {
				
			},
			deep: true
		}
	},
	mounted() {},
	methods: {
		
		putInstates(val){
			this.putInstate=val;
		},
		putIn(val){
			this.content=val;
		},
		
	}
};
